* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
}

li {
  list-style: none;
}

body {
  font-family: Montserrat, sans-serif;
}

.is-hidden {
  pointer-events: none;
  visibility: hidden;
  opacity: 0;
  display: none;
}

.label {
  color: #5c5c5c;
  flex-direction: column;
  gap: 8px;
  font-size: 10px;
  font-weight: 500;
  line-height: 1.2;
  display: flex;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .label {
    font-size: 14px;
    line-height: 1.21;
  }
}

@media screen and (min-width: 1280px) {
  .label {
    font-size: 14px;
    line-height: 1.21;
  }
}

.label__input {
  width: 100%;
  height: 35px;
  border: 1px solid #d7d7d7;
  border-radius: 4px;
  padding-left: 12px;
  padding-right: 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.25;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  .label__input {
    height: 49px;
    padding-left: 16px;
    padding-right: 16px;
    font-size: 14px;
    line-height: 1.21;
  }
}

@media screen and (min-width: 1280px) {
  .label__input {
    height: 49px;
    padding-left: 16px;
    padding-right: 16px;
    font-size: 14px;
    line-height: 1.21;
  }

  .label__input::placeholder {
    color: #0000004d;
  }
}

.section {
  padding-top: 70px;
  padding-bottom: 70px;
}

.section__title {
  color: #000;
  text-transform: uppercase;
  align-items: center;
  gap: 14px;
  margin-bottom: 32px;
  font-family: Montserrat;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 1.2;
  display: flex;
}

.section__title:after {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #000;
}

@media screen and (min-width: 768px) {
  .section__title {
    font-size: 44px;
  }
}

.container {
  max-width: 480px;
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.header {
  width: 100%;
  z-index: 2;
  background-color: #fff;
  padding-top: 16px;
  padding-bottom: 16px;
  position: fixed;
}

.header__wrapper {
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.header__box {
  gap: 14px;
  display: flex;
}

.header__nav {
  display: none;
}

@media screen and (min-width: 768px) {
