  z-index: 100;
  background-color: #e86f54;
  justify-content: center;
  align-items: center;
  transition: transform .25s ease-in-out;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(-100%);
}

.mobile-menu.is-open {
